* {
    list-style: none;
    text-decoration: none;
}

nav {
    background: #000;
    border-bottom: 1px solid rgb(75, 237, 0);
    padding: 10px 0;

    .block__nav_1 {
        display: flex;
        justify-content: space-between;
        align-items: center;

        ul {
            display: flex;
            color: #a9a8a8;
            gap: 2.5rem;

            li:hover {
                color: #fff;
            }
        }

        a {
            position: relative;

            span {
                position: absolute;
                top: -0;
                right: 0;
                background: rgb(75, 237, 0);
                width: 15px;
                height: 15px;
                border-radius: 50%;
                font-size: 10px;
                color: #000;
                font-weight: 700;
                display: flex;
                justify-content: center;
                align-items: center;

            }
        }
    }
}

section {
    background: #000;
    padding: 50px 0;

    .block__section_1 {
        display: flex;
        justify-content: center;
        gap: 2rem;
        align-items: center;

        h1 {
            color: #fff;
            font-weight: 500;
            font-size: 15px;
            display: flex;
            justify-content: center;
        }

        h1:hover {
            color: #44d62c;
        }
    }
}