* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.container {
    width: 80%;
    margin: auto;
}

header {
    background: #000;

    .parent {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        row-gap: 3rem;

        .card {
            width: 270px;
            height: 495px;
            background: #252525;

            img {
                width: 270px;
                height: 270px;
                background: #111;
            }

            h1 {
                font-size: 20px;
                color: #fff;
                font-weight: 400;
                padding: 0 10px;
                min-height: 100px;
            }

            p {
                color: #888;
                font-size: 15px;
                padding: 0 10px;
                display: flex;
                align-items: center;
                min-height: 60px;
            }

            .card__bottom {
                display: flex;
                justify-content: space-between;
                align-items: center;

                button {
                    margin-right: 10px;
                    width: 50px;
                    height: 25px;
                    border: none;
                    border-radius: 5px;
                    background: #44D62c;
                    color: #111;
                    font-size: 13px;
                    font-weight: 700;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    transition: all 2s ease;
                }

                button:hover {
                    width: 70px;
                    height: 28px;
                    transition: all 2s ease;
                    font-size: 15px;
                }
            }
        }
    }
}