* {
    background: #000;
}

.cart {
    text-align: center;

    h1 {
        padding-top: 40px;
        color: #fff;
        font-size: 20px;
        font-weight: 400;
        padding-bottom: 10px;
    }

    p {
        color: #999;
    }

    button {
        margin-top: 20px;
        background: #44d62c;
        padding: 15px 20px;
        font-size: 15px;
        font-weight: 700;
        border-radius: 8px;

        a {
            background: none;
            color: black;
        }
    }

    button:hover {
        background: #70f15c;
    }
}

.menu {
    display: flex;
    width: 80%;
    margin: 20px auto;
    padding: 20px 50px;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid #555;

    .left {
        display: flex;

        img {
            width: 100px;
            height: 100px;
        }

        .center__1 {
            padding-left: 20px;

            h1 {
                font-size: 20px;
                color: #EEEEEE;
            }

            p {
                color: #44d62c;
                width: 70%;
                padding-top: 20px;
            }
        }
    }


    .right {
        display: flex;

        p {
            color: #EEEEEE;
            padding-left: 100px;
            font-size: 20px;
            font-weight: 600;
        }

        .center__2 {
            display: flex;
            align-items: center;

            span {
                color: #EEEEEE;
                border: 1px solid #555;
                border-radius: 10px;
                padding: 10px 20px;
                margin: 0 10px;
            }

            button {
                background: #555;
                width: 30px;
                height: 30px;
                font-size: 20px;
                display: flex;
                justify-content: center;
                align-items: center;
                border-radius: 50%;
            }

            button:hover {
                background: #EEEEEE;
            }
        }
    }
}